$clip-distance: 0.05;
$main-color: #69ca62;
$remgika: #c7cda4;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Oswald", Arial, Helvetica, sans-serif;
  font-weight: 300;
  textarea {
    resize: none;
  }
}
a {
  outline: none;
  color: currentColor;
  text-decoration: none;
  &:hover {
    outline: none;
    color: currentColor;
    text-decoration: none;
  }
}
.App {
  text-align: center;
  min-width: 280px;
  // overflow: hidden;
  input:focus {
    outline: none;
  }
  button:focus {
    outline: none;
  }
  .notFound{
    min-height: 90vh;
  }
  /*********************************************************
  **********************************************************
  ********************* NAVIGATION HEADER ******************
  **********************************************************
  **********************************************************/
  div.navigation-block {
    position: sticky;
    top: 0;
    width: 100%;
    min-width: 280px;
    background-color: white;
    z-index: 2222;

    #navigation {
      top: 10px;
      height: 110px;
      color: black;
      width: 100%;
      min-width: 280px;
      z-index: 2222;
      display: flex;
      justify-content: center;
      justify-content: space-around;
      align-items: center;
      border-bottom: solid 2px #c7cda4;
      border-bottom-right-radius: 5px;
      border-bottom-left-radius: 5px;
      .logo {
        width: 120px;
        height: 100%;
        a {
          width: 100%;
          height: 100%;
          display: block;
          background-image: url("../img/remgika.png");
          background-size: cover;
          background-position: center;
        }
      }
      .left {
        #burger {
          margin: 0;
          padding: 0;
          width: 40px;
          font-size: 50px;
          cursor: pointer;
        }
      }
      .right {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .name {
          font-size: 0.9em;
          letter-spacing: 2px;
        }
        .avatar {
          height: 80px;
          width: 80px;
          border-radius: 50%;
          box-shadow: -3px -1.5px 6px #505dd89f, rgb(197, 135, 135) 3px 2px 6px;
          .image {
            width: 100%;
            height: 100%;
            background-position: top;
            background-repeat: no-repeat;
            background-size: cover;
            border-top-right-radius: 15px;
            border-top-left-radius: 15px;
            border-radius: 50%;
            border: rgb(226, 226, 226) solid 5px;
          }
        }
      }
    }
  }
  /*********************************************************
  **********************************************************
  ************************* SIDEBAR ************************
  **********************************************************
  **********************************************************/
  .sidebar {
    position: fixed;
    top: 110px;
    right: 0;
    background-color: $remgika;
    width: 200px;
    height: 100%;
    padding: 20px;
    padding-top: 0px;
    color: rgb(24, 16, 16);
    transform: translateX(100%);
    transition: all 0.2s;
    z-index: 122;
    .social {
      font-size: 1.4em;
      svg {
        margin-right: 10px;
        cursor: pointer;
      }
    }
    .menu-item {
      margin: 15px 2px;
      background-color: white;
      border-radius: 5px;
      width: 100%;
      // text-align: left;
      display: flex;
      justify-items: flex-end;
      align-items: center;
      flex-wrap: nowrap;
      min-height: 40px;
      a {
        color: rgb(27, 14, 14);
        text-decoration: none;
        min-width: fit-content;
        box-sizing: border-box !important;
        font-size: 100%;
        width: 100%;
        svg {
          margin-right: 5px;
          font-size: 25px;
        }
      }
    }
  }
  .slide-right {
    transform: translateX(-0%) !important;
  }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media (min-width: 768px) {
  .App {
    #navigation {
      justify-content: space-between;
      #burger {
        display: none;
      }
      .logo {
        justify-self: flex-start;
        align-self: flex-end;
      }
    }
    /*********************************************************
  **********************************************************
  ************************* SIDEBAR ************************
  **********************************************************
  **********************************************************/
    .navigation-block {
      display: flex;
      flex-direction: column;
      position: sticky;
      top: 0px;
      background-color: white;
      z-index: 222;
    }
    .sidebar {
      position: initial;
      background-color: $remgika;
      display: flex;
      width: 100%;
      padding: 1px;
      color: rgb(24, 16, 16);
      transform: none;
      z-index: 122;
      animation: none;
    }
  }
}

