
.articles-box{
  min-height: 80vh;
  margin-bottom: 50px;
}
.articles-items {


  margin-top: 10px;
  min-width: 280px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #f4f4f4;
  a {
    max-width: 210px;
    min-width: 150px;
    flex-basis: 0px;
    flex-grow: 1;
    flex-shrink: 1;
  }
}

.items-container {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 210px;
  min-width: 150px;
  margin: 5px;
  box-sizing: border-box;
  flex-basis: 0px;
  flex-grow: 1;
  flex-shrink: 1;
  margin-bottom: 10px;
  background-color: white;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.25);
  cursor: pointer;
  border-radius: 2px;
  .image {
    width: 100%;
    height: 150px;
    position: relative;
    img {
      width: 100%;
      height: 150px;
      object-fit: cover;
    }
    .itemOption {
      background-color: green;
      color: #fff;
      font-weight: bold;
      display: inline-block;
      padding: 2px 6px;
      position: absolute;
      right: 0px;
      bottom: 0;
      text-align: right;
      max-width: 100px;
      font-size: 13px;
      cursor: pointer;
      &::after {
        border-color: transparent;
        border-right-color: #678d20;
        border-width: 4px;
        bottom: -4px;
        right: 2px;
      }
      span {
        display: block;
        max-width: 9em;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
  .info {
    text-align: left;
    p {
      line-height: 1;
      font-weight: 400;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      display: block;
      padding: 2px;
    }
    span {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      display: block;
      font-size: 13px;
      padding: 2px;

      // margin-bottom: 5px;
    }
  }
}

