
$clip-distance: 0.05;
$main-color: #69ca62;
$remgika: #c7cda4;

.App{
      /*********************************************************
  **********************************************************
  ********************* WELCOME HOME PAGE ******************
  **********************************************************
  **********************************************************/
  .welcome {
    width: 100%;
    min-height: 100vh;
    max-height: max-content;
    background-image: url("../img/b.png");
    background-repeat: no-repeat;
    background-size: cover;
    text-align: left;
    position: sticky;
    /**********************************************************
    ************************ HEADER ***************************
    ***********************************************************/
    .welcom-header {
      height: 40px;
      text-align: left;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin: 15px;
      .login-logout {
        display: flex;
        flex-direction: row;
        .login-button {
          background-color: #c7cda4;
          margin-right: 10px;
          width: 90px;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 10px;
          a,
          a:visited {
            font-size: 20px;
            font-weight: 400;
            text-decoration: none;
            color: black;
          }
        }
        .logout-button {
          background-color: #c7cda4;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 10px;
          width: 90px;
          a,
          a:visited {
            font-size: 20px;
            font-weight: 400;
            text-decoration: none;
            color: black;
          }
        }
      }
      .menu {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0;
        padding: 0;
        height: 100%;
        width: 40px;
        text-align: center;
        font-size: 40px;
        background: linear-gradient(rgba(199, 205, 164, 0.787), #cacdf39f);
      }
    }
    /**********************************************************
  ************************ FIRST SECTION ********************
  ***********************************************************/
     .first-section.reverse{
       flex-direction: column-reverse;
     }
    .first-section {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 15px;
      img {
        width: 280px;
        margin: 0;
        padding: 0;
      }
      .remgika-texte {
        margin: 0;
        padding: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: 500px;
        h1{
          color: #bfc989;
          font-weight: bold;
        }
        h3 {
          color: rgb(104, 104, 104);
        }
        p {
          font-weight: 300;
          font-size: 20px;
          // text-align: center;
        }
        .soon{
          display: flex;
          flex-direction: column;
          margin-top: 10px;
          .title-soon{
            font-size: 20px;
            font-weight: 500;
          }
          div{
            display: flex;
            flex-wrap: wrap;
            p{
              margin: 10px;
              padding: 5px;
              background-color: rgba(0, 128, 0, 0.658);
              border-radius: 5px;
              color: white;
            }
          }
        }
      }
    }
    /**********************************************************
  ************************ SECOND SECTION *******************
  ***********************************************************/
    .second-section {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      
      height: 150px;
      .direct-home {
        background-color: white;

        width: 70%;
        height: 60px;
        max-width: 400px;
        border-radius: 20px;
        box-shadow: -3px -1.5px 6px #505dd89f, rgb(197, 135, 135) 3px 2px 6px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        a,
        a:visited {
          font-size: 20px;
          font-weight: 400;
          text-decoration: none;
          color: black;
        }
      }
    }
  }
}

@media  (min-width : 768px) {
  .App{
    .welcome { 
      .welcom-header{
        display: none;
      }
    }
  }  
}

@media  (min-width : 650px) {
  .App{
    .welcome { 
      .first-section.reverse{
        flex-direction: row;
      }
      .first-section{
        display: flex;
        flex-direction: row;
        .remgika-texte{
          width: 50%;
        }
        img{
          width: 50%;
        }
      }
    }
  }  
}